* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.timerBox {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.input {
  display: flex;
}

.timer {
  font-family: Rubik;
  display: flex;
}

.field {
  flex-direction: column;
  align-items: center;
  padding: 0 5px;
  display: flex;
}

.value {
  font-size: 65px;
  font-weight: 300;
}

.label {
  text-transform: uppercase;
  font-size: 13px;
  font-weight: 600;
}

.dots {
  font-size: 55px;
  font-weight: 300;
  position: relative;
  bottom: 2px;
}

input[type="text"] {
  width: 245px;
  background-color: #d3d3d3;
  border: 0;
  border-radius: 20px;
  padding: 10px;
  font-family: Rubik;
  font-size: 15px;
  font-weight: 400;
}

.button {
  width: 70px;
  color: #212121;
  background-color: salmon;
  border: 0;
  border-radius: 20px;
  margin-left: 5px;
  padding: 10px;
  font-family: Rubik;
  font-size: 15px;
  font-weight: 600;
  transition-property: all;
  transition-duration: .25s;
  transition-timing-function: ease-in-out;
}

.button:hover {
  background-color: #ff4c38;
}

.button:disabled {
  background-color: #fdaca3;
}

